<template>
	<view class="body">
		<view class="bodyBg"></view>
		<view class="content">
			<view class="title">{{data.title}}</view>
			
			<view v-if="type==4" class="">
				<u-collapse
				    @change="change"
				    @close="close"
				    @open="open"
					  :border="false"
				  >
					<view :class="{openBox:item.status,boxMargin:true}" v-for="(item,index) in data.data" :key="index">
						<u-collapse-item
						    :title="item.unit_name"
						    :name="index"
							
						>
						<view class="sentences" v-for="(a,b) in item.lds_subject" :key="b" @click="toStudy(a)">
							<u--text class="u-collapse-content" :text="b+1+'、'+a.title" lines="1"></u--text>
							<view v-if="a.question_state ==1" class="reSubject" @click.stop="reSubject(a)">重做</view>
						</view>
							
							<!-- <text class="u-collapse-content" v-for="(a,b) in item.lds_subject" :key="b">{{a.title}}</text> -->
						</u-collapse-item>
					</view>
				</u-collapse>
			</view>
			
			<view v-else class="sentence" v-for="(item,index) in data.data" :key="index" @click="toStudy(item)">
				<u--text :text="index+1+'、'+item.title" lines="1"></u--text>
				<view v-if="item.question_state ==1" class="reSubject" @click.stop="reSubject(item)">重做</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { listOption, subject } from '@/common/http.api.js';
	export default{
		data() {
			return {
				data:{},
				type:'' //题目类型
			}
		},
		onLoad(option) {
			this.type = option.type
			let token = store.state.vuex_token 
			listOption({token:token,type:option.type}).then(res=>{
				console.log(res)
				this.data = res.data.data
				uni.setStorageSync('type', res.data.data.type)
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			// 去学习
			toStudy(item) {
				// 保存大题ID
				this.$u.vuex('subject_id', item.id)
				uni.setStorage({
					key: 'difficulty',
					data: item.difficulty
				})
				let token = store.state.vuex_token 
				subject({token:token,subject_id:item.id}).then(res=>{
					console.log("跳转判断")
					console.log(res)
					console.log(res.data.data.subject_order)
					if(res.data.data.subject_order=='-1'){
						uni.navigateTo({
							url: '/pages/index/learnOne'
						})
					}else{
						uni.navigateTo({
							url: '/pages/index/learnOther'
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			// 重做
			reSubject(item) {
				this.$u.vuex('subject_id', item.id)
				uni.navigateTo({
					url: '/pages/index/learnOne?id='+item.id
				})
			},
			open(e) {
			  console.log('open1', e)
			  this.data.data[e].status = false
			},
			close(e) {
			  console.log('close', e)
			  this.data.data[e].status = true
			},
			change(e) {
			  console.log('change', e)
			}
		}
	}
</script>

<style lang="scss">
	.body{
		height: 100vh;
		position: relative;
		background-color: #f7f7fb;
		.bodyBg{
			width: 100vw;
			height: 100rpx;
			border-bottom-left-radius: 50%;
			border-bottom-right-radius: 50%;
			background-color: #ffd75c;
		}
		.content{
			position: absolute;
			top: 20rpx;
			left: 5%;
			width: 90%;
			height: 100vh;
			border-radius: 20rpx;
			background-color: #fff;
			.title{
				padding-top: 20rpx;
				font-size: 32rpx;
				line-height: 88rpx;
				font-weight: 500;
				text-align: center;
			}
			.sentences{
				display: flex;
				justify-content: space-between;
				align-items: center !important;
				.reSubject{
					    font-size: 20rpx;
					    background: linear-gradient(to right, #ffd23f, #fca51d);
					    line-height: 20px;
					    border-radius: 50rpx;
					    width: 80rpx;
					    color: #fff;
					    text-align: center;
					    height: 40rpx;
				}
			}
			.sentence{
				font-weight: 500;
				line-height: 88rpx;
				display: flex;
				justify-content: space-between;
				align-items: center !important;
				padding: 10rpx 30rpx;
				.reSubject{
					    font-size: 20rpx;
					    background: linear-gradient(to right, #ffd23f, #fca51d);
					    line-height: 20px;
					    border-radius: 50rpx;
					    width: 80rpx;
					    color: #fff;
					    text-align: center;
					    height: 40rpx;
				}
			}
			.openBox{
				border-bottom: 1px dashed #989898;
			}
			.boxMargin{
				margin: 0 30rpx;
			}
			.u-collapse-item{
				font-size: 34rpx;
				font-weight: 600;
				.u-cell__body{
					padding: 30rpx 0 10rpx 0 !important;
				}
				.u-icon__icon{
					border: 1px solid #909399;
					font-size: 28rpx !important;
					line-height: 28rpx !important;
					padding: 4rpx;
					border-radius: 50%;
				}
				.u-collapse-item__content__text{
					padding: 0 30rpx !important;
				}
				.u-text__value{
					line-height: 68rpx;
					font-size: 28rpx;
					color: #989898 !important;
					border-bottom: 1px dashed #989898;
					font-weight: 500;
				}
			}
		}
	}
</style>